.checkout-container {
    width: 100%;
    display: flex;
    justify-content: space-between;
    font-size: 0.9em;
}

.checkout-left-container {
    width: 48%;
}

.checkout-right-container {
    width: 48%;
}

.checkout-2 {
    display: block;
}

.checkout-1 {
    display: none;
}

@media screen and (max-width: 1280px) {
    .checkout-container {
        flex-direction: column;
    }

    .checkout-left-container {
        width: 100%;
    }

    .checkout-right-container {
        width: 100%;
    }

    .checkout-1 {
        display: block;
        margin-bottom: 1em;
    }

    .checkout-2 {
        display: none;
    }
}