.checkout-left-title-info {
    background-color: #f5f5f5;
    border-radius: 3px;
    padding: 0.75em;
    font-weight: bold;
    border: 1px solid rgba(0, 0, 0, 0.1);
    margin: 0;
}

.checkout-left-icon {
    font-size: 1.4em;
    margin-right: 0.3em;
    color: var(--second-bg-color);
}

.checkout-left-body-info {
    border: 1px solid rgba(0, 0, 0, 0.1);
    border-top: none;
    padding: 1em;
    padding-left: 2em;
    padding-right: 2em;
    font-size: 0.9em;
}

.checkout-input {
    display: flex;
}

.checkout-left-input-container {
    display: flex;
    margin-bottom: 1.2em;
}

.checkout-left-title {
    width: 20%;
    text-align: right;
    margin-top: 0.75em;
    margin-bottom: 0.75em;
    min-width: 100px;
}
.checkout-left-input-wrapper {
    width: 100%;
    margin-left: 2em;
}
.checkout-left-input {
    width: 100%;
    border: 1px solid rgba(0, 0, 0, 0.1);
    padding-left: 0.5em;
    border-radius: 4px;
    box-sizing: border-box;
    padding-top: 0.75em;
    padding-bottom: 0.75em;
}

.checkout-left-input:focus {
    outline: none;
}
.checkout-left-phone-container {
    width: 100%;
    display: flex;
    justify-content: space-between;
}

.inline-input {
    width: 48%;
}

.select-input {
    background-color: white;
}

.text-area-input {
    height: 5em;
    padding: 0.75em;
    box-sizing: border-box;
}

.checkout-left-body-checkbox {
    margin: 0;
    padding-left: 0.5em;
    line-height: 1.5em;
}

.checkout-left-button {
    border: none;
    padding: 1em;
    float: right;
    color: white;
    background-color: var(--main-bg-color);
}

.input-mobile-available {
    flex-direction: row;
    box-sizing: border-box;
}
.checkout-left-error {
    color: red;
    margin: 0;
    padding: 0.25em;
}

@media screen and (max-width: 768px) {
    .input-mobile-available {
        flex-direction: column;
    }

    .checkout-left-title {
        text-align: left;
    }

    .checkout-left-input-wrapper {
        margin-left: 0
    }

    .checkout-left-title {
        width: 100%;
    }
    .inline-input {
        width: 100%;
        margin-bottom: 1em;
    }
    .checkout-left-phone-container {
        flex-direction: column;
    }
}