.checkout-right-body {
    padding: 1em;
    border: 1px solid rgba(0, 0, 0, 0.1);
    border-top: none;
}

.checkout-right-table {
    width: 100%;
}

.checkout-right-cell {
    border: none;
    font-size: 0.9em;
    padding: 0.25em !important;
    box-sizing: border-box;
}

.cell-header {
    font-weight: bold;
    border-bottom: 1px dashed rgba(0, 0, 0, 0.1);
}

.checkout-right-name {
    font-weight: bold;
    font-size: 1.05em;
}

.checkout-right-name:hover {
    color: var(--second-bg-color);
}

.total-price-element {
    font-weight: bold;
    color: var(--third-bg-color);
    font-size: 0.9em;

}

.checkout-right-total {
    border: 1px solid rgba(0, 0, 0, 0.1);
    border-top: none;
    background-color: #f8f8f8;
}

.checkout-right-total {
    display: flex
}

.checkout-right-total-left {
    width: 100%;
    box-sizing: border-box;
    display: flex;
    justify-content: flex-end;
    margin-top: 1em;
}

.checkout-right-total-left-more {
    padding: 1em;
    margin: 0;
    color: var(--main-bg-color);
    border: 1px solid var(--main-bg-color);
    transition: color 0.1s ease-in, background-color 0.1s ease-in;
    margin-right: 1em;
}

.checkout-right-total-left-more:hover {
    background-color: var(--main-bg-color);
    color: white;
    cursor: pointer;
}

.checkout-right-total-right {
    width: 100%;
    padding: 1em;
}

.checkout-right-total-right-row {
    display: flex;
    font-size: 1em;
    padding: 0.4em;
}

.checkout-right-total-right-row-title {
    width: 50%;
    text-align: left;
}

.checkout-right-total-right-row-body {
    width: 50%;
    text-align: right;
}

@media screen and (max-width: 768px){
    .checkout-right-mobile {
        display: none;
    }
}

@media screen and (max-width: 1280px){
    .checkout-right-container-root {
        margin-top: 1em;
    }
}